import React from 'react'
import {
    BarChart, Bar, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend,
} from 'recharts';



class Barchart extends React.Component {
   

    constructor(props) {
        super(props);

        this.state = {
            data: [],
            ar: [],
            isLoad: true
        }
    }

    async componentDidMount() {

        let userOauth = JSON.parse(localStorage.getItem("myOauth"))
        this.access_token = userOauth.data.access_token
        await this.list('!B2:D')
    }

    list = async (value) => {

        try {

            this.list = await Sheetapi.getSheetValues(this.access_token, value)

            for (let i = 0; i < this.list.length; i++) {
                let value = await { name: this.list[i][0], ชาย60ปีขึ้นไป: this.list[i][1], หญิง60ปีขึ้นไป: this.list[i][2] }
                this.setState(prevState => ({
                    data: [...prevState.data, value]
                }))
            }
            this.state.isLoad = false
            console.log('data', this.state.data)
        } catch (err) {
            console.log(err);
        }

    }
    
    render() { 

        const { data, isLoad } = this.state
    
        return (
            <div className="warp-chart"> 
                <BarChart
                    width={500}
                    height={300}
                    data={data}
                    margin={{
                        top: 5, right: 30, left: 20, bottom: 5,
                    }}
                >
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="name" />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    <Bar dataKey="ชาย60ปีขึ้นไป" fill="#8884d8" />
                    <Bar dataKey="หญิง60ปีขึ้นไป" fill="#82ca9d" />
                </BarChart>
            </div>
        )
    }
}

export default Barchart;